#myChart {
  margin-top: 32px;
  width: 784px;
  /*height: 550px;*/
  /*margin: 5px 40px 5px 40px;*/
}

.info {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  align-content: center;
  flex-wrap: nowrap;
  border: 1px solid #04CCD9;
  margin-top: 32px;
}

tr {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: repeat(7, auto);
  grid-template-rows: auto;
  gap: 10px 10px;
  padding: 1px;
}

tr > *:nth-child(1) {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
}

tr > *:nth-child(2) {
  -ms-grid-row: 1;
  -ms-grid-column: 3;
}

tr > *:nth-child(3) {
  -ms-grid-row: 1;
  -ms-grid-column: 5;
}

tr > *:nth-child(4) {
  -ms-grid-row: 1;
  -ms-grid-column: 7;
}

tr > *:nth-child(5) {
  -ms-grid-row: 1;
  -ms-grid-column: 9;
}

tr > *:nth-child(6) {
  -ms-grid-row: 1;
  -ms-grid-column: 11;
}

tr > *:nth-child(7) {
  -ms-grid-row: 1;
  -ms-grid-column: 13;
}

td {
  padding: 1px;
  min-width: 150px;
  border: 1px solid black;
}

.error {
  /*border: 1px solid red !important;*/
  color: red !important;
  background-color: rgba(255, 0, 0, 0.18) !important;
}

button:disabled {
  opacity: 0.5;
}
